/* index.js */

var digitalocean = require('digitalocean');
var client = digitalocean.client('923f3fc880c616565b9c06b1c41ec4ebafaa65b2efd959f2ddc22805c871192b');

const AccountStatusButton = document.getElementById('accountStatus').addEventListener('click', accountStatus);
const dropletsListButton = document.getElementById('dropletsList').addEventListener('click', dropletsList);

function accountStatus() {
  client.account.get(function (err, account) {
    console.log(err); // null on success
    console.log(account); //
  });
}



function dropletsList() {
  client.droplets.list(function (page, perPage, ) {
    console.log(page); // null on success
    console.log(perPage); //
  });
}